.container {
  background-image: none;
  background-color: #fff;
}

.about_us_head {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.8)), url("../../Components/aboutus.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: white;
  font-size: 5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 5%;
  opacity: 0; /* Set initial opacity to 0 */
  transform: translateY(-50px); /* Initial position above the viewport */
  transition: opacity 1s ease-in-out, transform 1s ease-in-out; /* Add transition */
}

.about_us_head.visible {
  opacity: 1; /* Opacity when visible */
  transform: translateY(0); /* Final position */
}

.subhead {
  text-align: center;
  color: rgb(0, 0, 0);
  text-decoration: underline overline;
  text-decoration-style: solid;
  text-decoration-color: #686868;
  text-decoration-thickness: 2px;
  font-size: 1.5rem;
  margin-bottom: 3rem;
}

.mainbod {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(6, 4, 14, 0.5)), url("../../Components/satimage.jpeg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem;
  margin-bottom: 3rem;
  position: relative;
  transition: background-size 1s ease-in-out;
  min-height: 200px;
  font-family: serif;
  font-style: Garamond;
}

.mainbod:hover {
  background-size: 110%;
}

.text-container {
  width: 40%;
  text-align: justify;
  color: white;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

.feature-name {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1rem;
}

.feature-name p {
  color: #808080;
  font-size: 1.4rem;
  margin-top: 0.5rem;
}

p, .feature-name {
  color: white;
}

/* Transition effects */
.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

.slide-up {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.slide-up.visible {
  opacity: 1;
  transform: translateY(0);
}

.zoom-in {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.zoom-in.visible {
  opacity: 1;
  transform: scale(1);
}

/* Feature card layout */
.feature-card-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
  width: 100vw;
  position: relative;
}

.feature-card {
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5)), url("../../Components/featurecard.jpeg");
  font-style: Garamond;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem;
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  width: 100vw;
  opacity: 1;
}

.feature-card:hover {
  transform: scale(1.05);
}

.feature-card.fade-out {
  opacity: 0;
}

.prev-card, .next-card {
  background-color: #333;
  border: none;
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
  padding: 0.5rem 1rem;
  border-radius: 50%;
  position: absolute; /* Position arrows absolutely */
  z-index: 1;
}

.prev-card {
  left: 1rem; /* Position previous arrow on the left */
}

.next-card {
  right: 1rem; /* Position next arrow on the right */
}

.prev-card:hover, .next-card:hover {
  background-color: #666;
  transform: scale(1.2);
}

/* Footer Styles */
footer {
  background-color: #f2f2f2;
  padding: 2rem;
  text-align: center;
}

.footer-links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}

.footer-links a {
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #666;
}

.copyright {
  font-size: 0.9rem;
  color: #666;
}